import Vue from 'vue'
import Router from 'vue-router'
import MainPage from '@/components/MainPage'
import ORTool from '@/components/ORTool'
import Point from '@/components/Point'
import PMS from '@/components/PMS'
import API from '@/components/API'
import Import from '@/components/Import'
import Sample from '@/components/Sample'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/MainPage',
      name: 'MainPage',
      component: MainPage
    },
    {
      path: '/Point',
      name: 'Point',
      component: Point
    },
    {
      path: '/ORTool',
      name: 'ORTool',
      component: ORTool
    },
    {
      path: '/API',
      name: 'API',
      component: API
    },
    {
      path: '/PMS',
      name: 'PMS',
      component: PMS
    },
    {
      path: '/Import',
      name: 'Import',
      component: Import
    },
    {
      path: '/Sample',
      name: 'Sample',
      component: Sample
    },
  ]
})



