<template>
    <div>
        <el-row :gutter="20">
          <el-col :span="4" v-for="(item,index) in cardList" :key="index">
                <el-card :shadow="item.showdow" @click.native="cardClick(item)" :style="{ 'background-color': item.color }">
                  {{item.name}}<i :class="item.icon" style="margin-left:10px;"></i>
                </el-card>
          </el-col>
        </el-row>
    </div>
</template>

<script>
import DescList from '../components/DescList'
export default {
  name: "MainPage",
  data() {
    return {
      cardList: [
        { name: "專案管理系統", showdow: "hover", router:"PMS",desc:"專案管理系統",icon:"el-icon-document",color:'background-color:#CCEEFF'},
        { name: "通用匯入系統", showdow: "hover", router:"Import", desc:"通用匯入系統",icon:"el-icon-upload2",color:''},
        { name: "指向績效衡量工具", showdow: "hover", router:"Point",desc:"指向績效衡量工具",icon:"el-icon-top-right",color:''},
        { name: "ORTool", showdow: "hover", router:"ORTool",desc:"ORTool",icon:"el-icon-finished",color:''},
        { name: "API", showdow: "hover", router:"API", desc:"API",icon:"el-icon-connection",color:''}
        // { name: "Sample", showdow: "hover", router:"Sample", desc:"Sample",icon:"el-icon-connection",color:''},
      ],
      note:'',
    };
  },
  methods:{
    cardClick(item){
      this.cardList.forEach(function(obj){obj.color ='';})
      item.color = '#CCEEFF';
      this.$router.push(item.router).catch(err => {});
    },
  },
  mounted(){
    this.cardClick(this.cardList[0]);
  },
  components:{
    DescList
  }
};
</script>

<style scoped>
  .el-card{
    cursor: pointer;
  }
</style>



