<template>
    <div v-if="pictureList.length > 2">
      <elCarousel :pictureList="pictureList"></elCarousel>
    </div>
</template>

<script>
import elCarousel from '../components/PictureList'
export default {
   name: "PMS",
   data() {
    return {
        pictureList:[
          {name:'',image:'test'},
        ]
    };
  },
  components:{
    elCarousel
  }
}
</script>





