<template>
  <div>
     <el-input v-model="numNodes" placeholder="numNodes"></el-input>
     <el-input v-model="radius" placeholder="radius"></el-input>
     <div id="canvas" style="text-align:center;"></div>
  </div>
</template>

<script>
import * as d3 from 'd3';
export default {
   name: "Sample",
   data(){
     return{
       circleArray:[],
       numNodes:20,
       radius:200,
       elementRadius:10,
       elementStatus:{
          id:null,
       },
     }
   },
   methods:{
     createNodes(numNodes, radius){
       var nodes = [],
             width = (radius * 2) + 50,
             height = (radius * 2) + 50,
             angle,
             x,
             y,
             i;
         for (i=0; i<numNodes; i++) {
            angle = (i / (numNodes/2)) * Math.PI;
            x = (radius * Math.cos(angle)) + (width/2);
            y = (radius * Math.sin(angle)) + (width/2);
            nodes.push({'id': i, 'x': x, 'y': y});
         }
         return nodes;
     },
     createSvg(radius, callback){
        d3.selectAll('svg').remove();
        var svg = d3.select('#canvas').append('svg:svg')
                          .attr('width', (radius * 2) + 50)
                          .attr('height', (radius * 2) + 50);
        callback(svg);
     },
     createElements(svg, nodes, elementRadius){
        var _self  = this;
        var creation= Date.now();
        var element = svg.selectAll('circle')
                                .data(nodes)
                              .enter().append('svg:circle')
                                .attr('r', elementRadius)
                                .attr('cx', function (d, i) {
                                  return d.x;
                                })
                                .attr('cy', function (d, i) {
                                  return d.y;
                                }).on('mouseover', function() {
                                   _self.circleClick(this);
                                }).attr('id',function(x){
                                   return x.id;
                                });
     },
     draw(){
         var _self = this;
         var nodes = _self.createNodes(_self.numNodes, _self.radius);
         _self.createSvg(_self.radius, function (svg) {
           _self.createElements(svg, nodes, _self.elementRadius);
         });
     },
     circleClick(item){
        this.elementStatus.id = item.id;
        item.style.fill = "red";
     }
   },
   mounted(){
      this.draw();
   },
   watch:{
     'numNodes'(){
       this.draw();
     },
     'radius'(){
       this.draw();
     }
   }
}
</script>



