<template>
    <el-row>
        <el-carousel  type="card"  height="450px" :interval="4000">
          <el-carousel-item v-for="(item,index) in pictureList" :key="index">
            <el-image :src="require(`../assets/${item.image}.jpg`)" fit="fit" @click.native="handleViewImage(item)"></el-image>
          </el-carousel-item>
        </el-carousel>
        <el-dialog
              :title="DialogTitle"
              :visible.sync="centerDialogVisible"
              width="90%"
              center>
               <el-image :src="require(`../assets/${DialogPicture}.jpg`)" fit="fit" v-if="centerDialogVisible"></el-image>
        </el-dialog>
    </el-row>
</template>

<script>
export default {
  name: "pictureList",
  props:{
     pictureList: { required: true, type: Array}
  },
  data(){
    return{
      centerDialogVisible:false,
      DialogPicture:null,
      DialogTitle:'',
    }
  },
  methods:{
    handleViewImage(pictureItem){
      this.DialogPicture = pictureItem.image;
      this.DialogTitle = pictureItem.name;
      this.centerDialogVisible = true;
    }
  }
};
</script>




