<template>
  <el-collapse v-model="activeTab" accordion>
    <el-collapse-item v-for="(item,index) in descList" :key="index" :name="item.name">
      <template slot="title">
        <p style="margin-left:20px;">{{item.title}}</p>
      </template>
      <div class="Note">{{note}}</div>
    </el-collapse-item>
  </el-collapse>
</template>
<script>
const props = {
  name: "DescList",
  note: {
    required: true,
    type: String
  }
};
export default {
  props,
  data() {
    return {
      descList: [
        { tab: "1", title: "概述"},
        { tab: "2", title: "運用技術"},
        { tab: "3", title: "其它"}
      ],
      activeTab:'1',
    };
  }
};
</script>

<style scoped>
  .Note{
    margin-left:15px;
  }
</style>



